@import url(https://fonts.googleapis.com/css?family=Press+Start+2P);
@import url(https://fonts.googleapis.com/css?family=Nunito);
html, body, #root, .App {
  height: 100%;
  padding: 0px;
  margin: 0px;
}

body {
  font-family: "Press Start 2P";
  background: #9BE6E6;
  background-image: url(/hackathon_01/static/media/clouds.b18b28fd.png);
  background-size: 250px;
}

.App {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.end {
    width: 90%;
    max-height: 90%;
    max-width: 600px;
    font-size: 14px;
    line-height: 30px;
    text-align: center;
    padding: 20px 30px;
    padding-bottom: 40px;
    box-sizing: border-box;
}

.end h1 {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
    font-size: 30px;
    padding: 20px;
    box-sizing: border-box;
}

.end p {
    padding: 10px;
    box-sizing: border-box;
}

.heart {
    margin-bottom: 30px !important;
}
.home {
    background: #8BA200 !important;
    padding: 30px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    max-width: 800px;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    display: flex;
    flex-direction: column;
}

.home h1 {
    text-align: center;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

.title {
    display: inline-block;
    padding-left: 20px;
    font-size: 40px;
}

.home p {
    text-align: center;
    padding: 30px 0px;
    color: #033a0d;
    margin: auto;
}

.start-button {
    margin: auto;
}

.lessons {
    background: white;
    min-width: 600px;
    width: 90%;
    max-width: 700px;
    overflow: scroll;
    max-height: 95%;
    min-height: 600px;
    padding: 0px;
}

.lessons h1 {
    padding: 30px;
}

.lessons img {
    max-width: 100%;
}

.lessons p {
    padding: 10px 30px;
    font-size: 20px;
    line-height: 30px;
    font-family: "Nunito";
}

.challenge {
    font-weight: bold;
    font-size: 30px !important;
    line-height: 40px !important;
}

.lessons p span {
    padding: 0px 5px;
    background: #000819 !important;
}

.lessons .code {
    display: block;
    background: #000819;
    color: #5d78b3;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 10px auto;
    padding: 10px;
    font-family: 'Nunito';
    font-size: 20px;
    line-height: 30px;
}


.code-editor {
    position: relative;
    width: 100%;

    display: flex;
    flex-direction: column;
    overflow: hidden;
}


.input-wrapper, .output-wrapper {
    margin: 0px;
    padding: 0px;
    display: flex;
}

.output-wrapper {
    background: black;
    color: rgb(60, 255, 60);
    font-size: 14px;
    line-height: 25px;
    padding-bottom: 50px !important;
    border-top: 2px solid rgb(60, 255, 60);
    height: 150px;
}

.output-wrapper button {
    color: rgb(60, 255, 60);
    bottom: 0px;
}

.output-wrapper button, .submit-code  {
    right: 0px;
    position: absolute;

    margin: 10px;
    padding: 5px 10px;
    box-sizing: border-box;
    text-transform: uppercase;
    font-family: "Press Start 2P";
    font-size: 12px;
}

.submit-code {
    top: 0px;
    border: 1px solid #5d78b3;
    color: #5d78b3;
    background: none;
    border-radius: 5px;
    z-index: 2;
}

.submit-code:hover {
    color: white;
    border-color: white;
}

.textarea-output, textarea {
    height: 300px;
    background: none;
    border: none;
    font-size: 18px;
    line-height: 30px;
    padding-top: 50px !important;
}

.textarea-output, textarea, .output-wrapper {
    margin: 0px;
    padding: 30px;
    box-sizing: border-box;

    font-family: "Press Start 2P";
    text-align: left;
    word-break: break-all;

    width: 100%;
}

textarea {
    color: transparent;
    caret-color:white;
    resize: none;
    outline: none;
    z-index: 1;
}

textarea::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgb(93 120 179);
    opacity: 1; /* Firefox */
}

textarea:-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgb(93 120 179);
    opacity: 1; /* Firefox */
}

textarea::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgb(93 120 179);
    opacity: 1; /* Firefox */
}

.textarea-output {
    position: absolute;
    top: 0px;
    left: 0px;

    background: #000819;
    color: #5d78b3;
}


.variable {
    color: #9bd5d8;
}

.function {
    color: white;
}

.statement {
    color: #d49cc7;
}

.string {
    color: #9de29d;
}

.number {
    color: orange;
}

.array {
    color: #ffe8a6;
}

.boolean {
    color: rgb(237, 149, 237);
}

.array-challenge {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 100px !important;
}
.icons {
    width: 350px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
}

.world-path {
    padding: 40px;
    padding-top: 50px;
    box-sizing: border-box;
    background: #DAC8AC !important;
    color: #52351b !important;
    min-width: 600px;
    max-width: 600px;
    display: flex;
    flex-direction: column;
}

.path-wrapper {
    margin-top: 50px;
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.path, .path-left {
    position: absolute;
    display: inline-block;
    height: 10px;
    width: 100%;
}

.path {
    background: #D69760;
    width: 25%;
}

.path-left {
    background: #BAA482;
    width: 75%;
    right: 0px;
}

.path-milestone {
    width: 30px;
    height: 30px;
    z-index: 2;
}

.path-labels {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 10px;
    margin-bottom: 20px;
}

.path-label {
    font-size: 12px;
    padding: 10px;
}

.path-label:nth-child(1){
    padding-left: 0px;
}

.path-label:last-child{
    padding-right: 0px;
}

.character-position{
    width: 100%;
    position: relative;
    z-index: 3;
}

.castle {
    position: absolute;
    width: 100px;
    right: -40px;
    top: -28px;
}

.player {
    position: absolute;
    top: -30px;
    left: 0px;
}
